import React, { useState, useEffect } from 'react'

import './App.css'
import yamaxun from './images/yamaxun.jpg'
import kouhong from './images/kouhong.jpg'
import kouzhao from './images/kouzhao.jpg'
import bingqiling from './images/bingqiling.jpg'

const YAMAXUN = 1
const KOUHONG = 2
const KOUZHAO = 3
const BINGQILING = 4
const IMG_KEY = [
	{
		key: YAMAXUN,
		value: yamaxun,
	},
	{
		key: KOUHONG,
		value: kouhong,
	},
	{
		key: KOUZHAO,
		value: kouzhao,
	},
	{
		key: BINGQILING,
		value: bingqiling,
	},
]
function App() {
	const random = Math.ceil(Math.random() * 3 + 1)
	const [imgIndex, setImgIndex] = useState(1)
	const [isFirst, setIsFirst] = useState(false)
	useEffect(() => {
		if (!isFirst) {
			var timer1 = setTimeout(() => {
				setImgIndex(random)
				setIsFirst(true)
			}, 8000)
		} else {
			clearTimeout(timer1)
		}
	}, [isFirst])
	return (
		<div className="App">
			<img className="img" alt="" src={IMG_KEY[imgIndex - 1].value}></img>
		</div>
	)
}

export default App
